.form__fieldset {
  position: relative;
  margin: 0;
  padding: 0;
  border: none;

  @media (min-width: $tablet-width) {
    padding-top: 30px;
    padding-bottom: 20px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 35px;
    padding-bottom: 15px;
  }
}

.form__fieldset-grid {
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 3fr;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 5fr;
  }
}

.form__legend {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 17px;
  line-height: 16px;
  color: $basic-blue;

  @media (max-width: $tablet-width - 1px) {
    width: 100%;

    & span {
      display: flex;
    }

    & span::after {
      content: "";
      display: flex;
      align-self: center;
      flex-grow: 1;
      flex-shrink: 1;
      margin-left: 20px;
      height: 1px;
      width: 60%;
      background-color: $special-extra-blue;
    }
  }

  @media (min-width: $tablet-width) {
    margin-top: 16px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 60px;
  }
}

