.form__input {
  display: block;
  width: 100%;
  padding: 16px 18px 16px 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-size: 17px;
  line-height: 18px;
  color: $basic-dark;
  border: none;
  border-bottom: 2px solid $basic-black;
  box-sizing: border-box;
  background-color: transparent;

  @media (min-width: $tablet-width) {
    padding-left: 16px;
    font-size: 19px;
    line-height: 18px;
  }
}

.form__input:hover,
.form__input:focus {
  border-bottom: 2px solid $special-extra-blue;
}

.form__input:focus-visible {
  outline: none;
}

.form__input::placeholder {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-size: 17px;
  line-height: 18px;

  @media (min-width: $tablet-width) {
    font-size: 19px;
    line-height: 18px;
  }
}

.form__input:focus::placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.form__list {
  margin: 0;
  padding: 0;
  padding-top: 24px;
  list-style: none;

  @media (min-width: $tablet-width) {
    padding-top: 0;
  }
}

.form__title {
  display: block;
  margin: 0;
  padding: 0 0 33px 0;
  font-size: 14px;
  line-height: 16px;
  color: $basic-dark;

  @media (min-width: $tablet-width) {
    padding-top: 10px;
    font-size: 17px;
    line-height: 30px;
    order: 1;
    justify-self: end;
  }

  @media (min-width: $desktop-width) {
    padding-top: 13px;
  }
}

.form__submit-wrapper {
  margin-bottom: 55px;

  @media (min-width: $tablet-width) {
    margin-bottom: 28px;
    display: grid;
    grid-template-columns: 230px 1fr;
    // grid-template-rows: min-content;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: 230px 1fr 174px;
  }
}

.form__button {
  min-width: 260px;
  font-size: 17px;
  line-height: 16px;

  @media (min-width: $tablet-width) {
    min-width: 230px;
    height: 52px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 175px;
  }
}
