.features {
  padding-top: 32px;
  padding-bottom: 5px;

  @media (min-width: $tablet-width) {
    padding-top: 60px;
    padding-bottom: 18px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 65px;
    padding-bottom: 25px;
  }
}

.features__wrapper {
  @media (min-width: $desktop-width) {
    margin-left: 225px;
    margin-right: 200px;
    max-width: calc(100% - 225px - 200px);
  }
}

.features__title {
  margin: 0;
  margin-bottom: 28px;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;

  @media (min-width: $tablet-width) {
    margin-bottom: 41px;
    font-size: 43px;
    line-height: 50px;
  }

  @media (min-width: $desktop-width) {
    font-size: 53px;
    line-height: 57px;
  }
}

.features__list {
  display: grid;
  grid-template-columns: 1fr 1.2fr;
  column-gap: 60px;
  grid-template-rows: repeat(auto-fit, min-content);
  margin: 0;
  padding: 0;
  list-style: none;

  @media (min-width: $desktop-width) {
    max-width: 700px;
    grid-template-columns: 1fr 1.2fr;
  }

  @media (max-width: $tablet-width - 1px) {
    grid-template-columns: 1fr;
  }
}

.features__item {
  position: relative;
  padding-top: 54px;
  padding-bottom: 32px;
  font-weight: bold;
  font-size: 17px;
  line-height: 20px;

  @media (min-width: $tablet-width) {
    padding-bottom: 60px;
    font-weight: bold;
    font-size: 20px;
    line-height: 22px;
  }
}

.features__item::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  mask-repeat: no-repeat;
  mask-size: cover;
  mask-position: 50% 50%;
  background-color: $basic-blue;
}

.features__item:nth-of-type(2n + 2) {
  @media (max-width: 320px) {
    order: 1;
    padding-left: 0;
  }
}

.features__item--icon-1 {
  @media (max-width: $mobile-width-only) {
    padding-bottom: 30px;
  }

  &::before {
    left: 7px;
    width: 40px;
    height: 38px;
    mask-image: url("../img/stack.svg#icon-1");
  }
}

.features__item--icon-2 {
  @media (max-width: $mobile-width-only) {
    padding-bottom: 33px;
    padding-top: 51px;
  }

  &::before {
    width: 35px;
    height: 34px;
    mask-image: url("../img/stack.svg#icon-2");
  }
}

.features__item--icon-3 {
  @media (max-width: $mobile-width-only) {
    padding-bottom: 35px;
  }

  &::before {
    width: 47px;
    height: 27px;
    mask-image: url("../img/stack.svg#icon-3");
  }
}

.features__item--icon-4 {
  @media (max-width: $mobile-width-only) {
    padding-top: 49px;
  }

  &::before {
    left: -7px;
    width: 55px;
    height: 23px;
    mask-image: url("../img/stack.svg#icon-4");
  }
}

.features__item--icon-5 {
  @media (max-width: $mobile-width-only) {
    padding-top: 48px;
  }

  &::before {
    left: 7px;
    width: 34px;
    height: 27px;
    mask-image: url("../img/stack.svg#icon-5");
  }
}

.features__item--icon-6::before {
  width: 33px;
  height: 32px;
  mask-image: url("../img/stack.svg#icon-6");
}
